import { Component, OnInit } from '@angular/core';
import { GridDataResult, PageChangeEvent, SelectionEvent, SelectableSettings} from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { TodoService } from '../todo.service';
//import {WorktitemInfo} from './task';
import { formatDate } from '@telerik/kendo-intl';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-todo',
  templateUrl: './todo.component.html',
  styleUrls: ['./todo.component.css']
})
export class TodoComponent implements OnInit {
  public gridView: GridDataResult;
  todoList: any[] = new Array<any>();
  private pageIndex=1;
  public pageSize = 10;
  public skip = 0;
  public selectedWorkitem;
  public enable=true;
  public selectableSettings: SelectableSettings = {
    enabled: true,
    mode: 'single'
  };

  constructor(private todoService:TodoService, private router: Router, private actRoute: ActivatedRoute,

  ) { 
    //this.todoViewModule=new ToDoViewModel();
  }
  // navigateTTo(){
  //   this.actRoute.queryParams=this.selectedWorkitem;
  //   this.router.navigateByUrl('main/todo/approval');
  // }
  getToDoList():void{
    this.todoService.getToDoList(this.skip/this.pageSize+1,this.pageSize)
    .subscribe(value => {
      console.log(value);
      this.todoList = value.result;
      this.todoList.forEach(element => {
        element.startedDate=formatDate(new Date(element.startedDate), "yyyy/MM/dd HH:mm:ss");
        element.completedDate=formatDate(new Date(element.completedDate), "yyyy/MM/dd HH:mm:ss");
      });
      this.gridView = {
        data: this.todoList,
        total: value.totalCount
      };  
    });
  }

  public pageChange(event: PageChangeEvent): void {
    this.skip = event.skip;
    this.getToDoList();
  }
  
  OnSelectionChange(event: SelectionEvent ):void{
    this.enable=false;
    
    if(event.selected) {
      this.selectedWorkitem=event.selectedRows[0].dataItem;
      //this.todoService.cache["currentItem"] = this.selectedWorkitem;
      //this.todoService.setSelectedRow(event.selectedRows[0].dataItem);
    }
  }
  ngOnInit() {
    this.getToDoList();
    
  }

}



