import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpService } from '@gsp-sys/rtf-common/common-shared/services/httpservice/http.service';
// import { HttpClient, HttpHeaders } from '../../../library/angular.common.http';
import { WorktitemInfo } from './todo/task';
import { List } from 'immutable';

@Injectable()
export class TodoService {
  private heroesUrl = '/api/runtime/wf/v1.0';
  // private heroesUrl ='http://localhost:5000/api/runtime/wf/v1.0'
  // private subject: Subject<WorktitemInfo> = new Subject<WorktitemInfo>();
  // private subject: Subject<any> = new Subject<any>();
  cache: any = {};

  constructor(private http: HttpService) {

  }

  getToDoList(index: number, size: number): Observable<any> {
    const url = `${this.heroesUrl}/workitem/tasks?pageIndex=${index}&pageSize=${size}`;
    return this.http.get(url);
    // return this.http.request('get',url);
  }

  startWorkflow(procDefKey: string, procInsName: string, procInsSchema: any, bizID: string, description: string): Observable<any> {
    const url = `${this.heroesUrl}/wfrt/startWorkflowByKey/${procDefKey}`;

    // 改用框架封装的httpservice，暂时屏蔽
    // const httpOptions = {
    //   headers: new HttpHeaders({
    //     'Content-Type':  'application/json',
    //     'Access-Control-Allow-Origin': '*'
    //   })
    // };

    return this.http.post(url, {
      'ProcessInstanceName': procInsName,
      'Description': description,
      'bizId': bizID,
      'paras': procInsSchema
    });
  }

  approval(workitemID: string, circulationType: string): Observable<any> {
    const url = `${this.heroesUrl}/wfrt/complete/${workitemID}`;
    // const httpOptions = {
    //   headers: new HttpHeaders({
    //     'Content-Type':  'application/json',
    //     'Access-Control-Allow-Origin': '*'
    //   })
    // };
    return this.http.post(url, { 'CirculationType': circulationType });
  }

  AddPreParticipant(workitemID: string, participantList: List<string>): Observable<any> {
    const url = `${this.heroesUrl}/wfrt/AddPreParticipant/${workitemID}`;

    return this.http.post(url, participantList);
  }
}



