import { Component, OnInit } from '@angular/core';
import { TodoService } from '../todo.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tododetail',
  templateUrl: './tododetail.component.html',
  styleUrls: ['./tododetail.component.css']
})
export class TodoDetailComponent implements OnInit {
  public opened = false;
  private description:string;
  private bizID:string;
  private procInsName:string;
  private procDefKey:string;
  private procInsSchema:any;

  constructor(private todoService:TodoService,private router: Router) { 
  }

  public close() {
    this.opened = false;
  }

  public open() {
    this.opened = true;
  }

  ngOnInit() {
  }
  
  updateProcInsName(value: string) { this.procInsName = value; }
  updateBizID(value: string) { this.bizID = value; }
  updateDescription(value: string) { this.description = value; }
  updateProcDefKey(value:string){this.procDefKey = value; }
  updateProcInsSchema(value:any){this.procInsSchema = value; }

  startWorkflow(){
    let para = JSON.parse(this.procInsSchema);
    this.todoService.startWorkflow(this.procDefKey,this.procInsName,para,this.bizID,this.description).subscribe(value=>{
      if(value.processInstanceId!=null && value.processInstanceId!=""){
        alert("启动流程成功，流程实例ID为："+value.processInstanceId);
      }
    });
  }

  navigateTo(url: string) {
    if (url) {
      this.router.navigateByUrl(url);
    }
  }

}



