import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { WorktitemInfo } from '../todo/task';
import { TodoService } from '../todo.service';
//import { Subscription } from '../../../../library/rxjs.lib';
import { ActivatedRoute, Params, ParamMap, Router } from '@angular/router';
//import { switchMap } from '../../../../library/rxjs.lib';

@Component({
  selector: 'app-approval',
  templateUrl: './approval.component.html',
  styleUrls: ['./approval.component.css']
})
export class ApprovalComponent implements OnInit,OnDestroy {
  private name: string;
  private submitter:string;
  private startedDate:Date;
  private workitemID:string;
  private actDefID:string;
  public circulateBtn:string;
  public rejectBtn:boolean=false;
  public AddPreParticipantBtn:boolean=false;
  private terminateBtn:string;
  private isStartAct:boolean;
  //subscription: Subscription;
  public SPYLList: Array<string> = [
    '通过', '不通过'
  ];
  public listItems: Array<string> = ['lizhichao', 'wangjiazhao', 'zhoulijie',  'taoshunhang'];
  public dropdownValue:any;
  constructor(
    private todoService: TodoService,
    private actRoute: ActivatedRoute,
    private router: Router
  ) {
    // this.subscription=this.todoService.getSelectedRow().(value=>{
    //   console.log(value);
    //   this.workitemName=value.name;
    // })
    // this.workitem = this.todoService.cache["currentItem"];
    
    // this.todoService.cache = {};

    this.actRoute.queryParams.subscribe((params: Params) => {
      this.name = params['name'];
      this.submitter=params['creator'];
      this.startedDate=params['startedDate'];
      this.workitemID=params['id'];
      this.actDefID=params['activityDefinitionId'];
      if(this.actDefID=="startEvent")
      {
         this.circulateBtn="重新提交";
         this.rejectBtn=true;
         this.isStartAct=true;
      }
      else{
        this.circulateBtn="审批";
        this.rejectBtn=false;
        this.isStartAct=false;
      }
    });
    
    //this.name= this.route.snapshot.params['id'];
   }
   ngOnInit(): void {
  }

  ngOnDestroy() {
    //this.subscription.unsubscribe();
  }

  approval(workitemID:string,circulationType:string){
    this.todoService.approval(this.workitemID,circulationType).subscribe(value=>{
      if(value.taskId!=null && value.taskId!=""){
        let message = "";
        switch(circulationType){
          case "Circulate":
            message=this.isStartAct?"重新提交成功！":"审批成功，工作项ID为："+value.taskId;
            alert(message);
            break;
          case "Reject":
            alert("已成功驳回！");
            break;
          case "Terminate":
            message=this.isStartAct?"放弃重新提交，流程终止！":"流程终止！";
            alert(message);
            break;
          default:
            break;
        }
        this.router.navigateByUrl('main/todo')
      }
    });
  }

  AddPreParticipant(){
    this.todoService.AddPreParticipant(this.workitemID,this.dropdownValue).subscribe(value=>{
        alert("加签成功，当前工作项id为："+value.taskId);
        this.router.navigateByUrl('main/todo')
    });
  }
  navigateTo(url: string) {
    if (url) {
      this.router.navigateByUrl(url);
    }
  }
}



